import React from "react";
// import cardPic from "../images/katie-zaferes.png";
import star from "../images/star.png";


export default function Card(props){
    let spotsText;
    if(props.openSpots === 0){
        spotsText = "SOLD OUT"
    } else if(props.location === "Online" ) {
        spotsText= "ONLINE"
    }
    return(
        <div className="card">
            {spotsText && <div className="status">{spotsText}</div>}
            <img src= {require(`../${props.coverImg}`)} className="card-main-image" alt="Katie-Zaferes-img"/>
            <span className="card-rating">
                <img src={star} className="card-star-icon" alt="star-icon"/>
                <span>{props.stats.rating}</span>
                <span className="grey">({props.stats.reviewCount}) •</span>
                <span className="grey">{props.location}</span>
            </span>

            <p className="card-title">{props.title}</p>
            <p className="card-price"><span className="bold">From ${props.price}/</span> <span className="grey">person</span> </p>
        </div>
    )

}